.vdl-grid-list {
  display: block;
  position: relative; }

.vdl-grid-tile {
  display: block;
  position: absolute;
  overflow: hidden; }
  .vdl-grid-tile .vdl-figure {
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 0;
    margin: 0; }
  .vdl-grid-tile .vdl-grid-tile-header,
  .vdl-grid-tile .vdl-grid-tile-footer {
    display: flex;
    align-items: center;
    height: 48px;
    color: #fff;
    background: rgba(0, 0, 0, 0.38);
    overflow: hidden;
    padding: 0 16px;
    font-size: 16px;
    position: absolute;
    left: 0;
    right: 0; }
    .vdl-grid-tile .vdl-grid-tile-header [vdl-line],
    .vdl-grid-tile .vdl-grid-tile-footer [vdl-line] {
      white-space: nowrap;
      overflow-x: hidden;
      text-overflow: ellipsis;
      display: block;
      box-sizing: border-box; }
      .vdl-grid-tile .vdl-grid-tile-header [vdl-line]:nth-child(n+2),
      .vdl-grid-tile .vdl-grid-tile-footer [vdl-line]:nth-child(n+2) {
        font-size: 12px; }
    .vdl-grid-tile .vdl-grid-tile-header > *,
    .vdl-grid-tile .vdl-grid-tile-footer > * {
      margin: 0;
      padding: 0;
      font-weight: normal;
      font-size: inherit; }
    .vdl-grid-tile .vdl-grid-tile-header.vdl-2-line,
    .vdl-grid-tile .vdl-grid-tile-footer.vdl-2-line {
      height: 68px; }
  .vdl-grid-tile .vdl-grid-list-text {
    display: flex;
    flex-direction: column;
    width: 100%;
    box-sizing: border-box;
    overflow: hidden; }
    .vdl-grid-tile .vdl-grid-list-text > * {
      margin: 0;
      padding: 0;
      font-weight: normal;
      font-size: inherit; }
    .vdl-grid-tile .vdl-grid-list-text:empty {
      display: none; }
  .vdl-grid-tile .vdl-grid-tile-header {
    top: 0; }
  .vdl-grid-tile .vdl-grid-tile-footer {
    bottom: 0; }
  .vdl-grid-tile .vdl-grid-avatar {
    padding-right: 16px; }
    [dir='rtl'] .vdl-grid-tile .vdl-grid-avatar {
      padding-right: 0;
      padding-left: 16px; }
    .vdl-grid-tile .vdl-grid-avatar:empty {
      display: none; }
